<template>
  <div id="historyDetail" :style="{
  'background': `url('static/smogBg/history.jpg') no-repeat 0 0 / cover fixed`}">
    <div class="inner">
      <h2>{{this.$route.query.city}}历史雾霾 {{this.$route.query.time.replace('-','年')}}月份</h2>
      <ul class="clearfix">
        <li class="title clearfix">
          <span>日期</span>
          <span>空气状况</span>
          <span>AQI</span>
          <span>PM2.5</span>
          <span>PM10</span>
          <span>SO<sub>2</sub></span>
          <span>NO<sub>2</sub></span>
          <span>CO</span>
          <span>O<sub>3</sub></span>
        </li>
        <li class="con clearfix" v-for="item in detailList">
          <span>{{item.time}}</span>
          <span>{{item.status}}</span>
          <span>{{item.aqi}}</span>
          <span>{{item.pm25}}</span>
          <span>{{item.pm10}}</span>
          <span>{{item.so2}}</span>
          <span>{{item.no2}}</span>
          <span>{{item.co}}</span>
          <span>{{item.o3}}</span>
        </li>
      </ul>
      <loading v-if="!detailList.length && !error"></loading>
      <wrong v-if="error"></wrong>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { getSmogHistoryList } from 'api/getHistoryList'
  import { ERROR } from 'api/config'

  import Loading from 'base/loading/loading'
  import Wrong from 'base/error/error'

  export default {
    name: 'history-detail',
    data () {
      return {
        detailList: [],
        error: false
      }
    },
    methods: {
      _getHistoryList () {
        getSmogHistoryList(this.$route.query.city, this.$route.query.time).then((res) => {
          if (res.data.error === ERROR) {
            this.detailList = res.data.data
            this.error = false
          } else {
            this.error = true
          }
        }).catch((res) => {
          this.error = true
        })
      }
    },
    mounted () {
      this._getHistoryList()
    },
    components: {
      Loading,
      Wrong
    }
  }

</script>

<style lang="less" scoped rel="stylesheet/less" type="text/less">
  #historyDetail {
    color: #fff;
    min-height: 100%;
    padding-top: 100px;
    padding-bottom: 20px;
    box-sizing: border-box;
    transition: all 2s;
    font-size: 13px;
    h2 {
      font-size: 28px;
      text-align: center;
      padding: 10px 0;
      box-sizing: border-box;
    }
    ul {
      width: 100%;
      background-color: rgba(0, 0, 0, .5);
      li {
        width: 100%;
        padding: 5px 0;
        box-sizing: border-box;
        border-bottom: 1px solid #eee;
        display: flex;
        span {
          float: left;
          display: inline-block;
          width: calc(100% / 4);
          padding: 0 10px;
          box-sizing: border-box;
        }
      }
      .title {
        width: 100%;
        span {
          font-size: 14px;
          font-weight: bold;
          letter-spacing: 2px;
        }
      }
      .con {
        width: 100%;
        span {
          padding: 10px;
          box-sizing: border-box;
        }
      }
    }
  }
</style>


